import Vue from "vue"
import VueRouter from "vue-router"

import Home from "../views/Home.vue"
import Signin from "../views/SignIn.vue"
import SignUp from "../views/SignUp.vue"
import MyCourse from "../views/MyCourse.vue"
import AboutCourse from "../views/AboutCourse.vue"
import hello from "../components/HelloWorld.vue"
import prac from "../components/forprac.vue"

import E404 from "../views/E404.vue"

import User_Header from "../components/User_Header.vue"
import Main_Header from "../components/Main_Header.vue"

import Editor from "../components/Editor/Editor.vue"

Vue.use(VueRouter)

const routes = [
  {
    path: "*",
    name: "E404",
    components: {
      body: E404,
    },
  },
  {
    path: "/editor/:course_code",
    components: {
      body: Editor,
    },
  },
  {
    path: "/prac",
    components: {
      body: prac,
    },
  },
  {
    path: "/",
    name: "Home",
    components: {
      header: Main_Header,
      body: Home,
    },
  },
  {
    path: "/signin",
    name: "Signin",
    //beforeEnter: isAuthenticated(),
    //to from router       .
    //next      .         .
    //component beforeRouterEnter    . beforeRouterleave .
    components: {
      header: Main_Header,
      body: Signin,
    },
  },
  {
    path: "/signup",
    name: "SignUp",
    components: {
      header: Main_Header,
      body: SignUp,
    },
  },
  {
    path: "/mycourse",
    name: "MyCourse",
    components: {
      header: User_Header,
      body: MyCourse,
    },
  },
  {
    path: "/mycourse/:course_code",
    name: "AboutCourse",
    components: {
      header: User_Header,
      body: AboutCourse,
    },
  },
  
]

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
})

export default router
